.c-documents .c-reporting {
  position: absolute;
  right: 0;
  bottom: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.c-documents .c-reporting .c-button {
  padding: 0;
  background: #eeeeee;
}

.c-documents .c-reporting .c-dropdown {
  position: absolute;
  z-index: 1000;
  right: 10px;
  top: 97%;
  padding: 10px;
  background: white;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
  -webkit-transition: -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  -o-transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1), -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  white-space: nowrap;
}

.c-documents .c-reporting .c-dropdown .c-dropdown--header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;

  h3 {
    font-weight: normal;
    line-height: 30px;
  }
}

.c-documents .c-reporting .c-dropdown .c-dropdown--select {
  position: relative;
  display: inline-block;
  margin-bottom: 15px;
  width: 100%;
}

.c-documents .c-reporting .c-dropdown .c-dropdown--select select {
  display: block;
  width: 100%;
  cursor: pointer;
  padding: 10px 5px;
  outline: 0;
  border: 0;
  border-radius: 0;
  background: #eeeeee;
  color: black;
  -webkit-appearance: none;
  -moz-appearance: none;
  font-size: smaller;
  font-weight: lighter;
}

.select--arrow {
  position: absolute;
  top: 13px;
  right: 13px;
  width: 0;
  height: 0;
  pointer-events: none;
  border-style: solid;
  border-width: 8px 5px 0 5px;
  border-color: black transparent transparent transparent;
}

.c-documents .c-reporting .c-dropdown textarea {
  width: 100%;
  display: block;
  font-size: smaller;
  font-weight: lighter;
  border-color: #dddddd;
}



