.outerContainer {
  padding: 20px;
}

.outerContainer > .c-container {
  width: 100%;
  background: none;
  text-align: center;
  margin: 0;
}

.c-documents {
  width: 85%;
  margin: 0 auto;
}

.c-documents .c-container {
  margin: 0;
  width: 100%;
  position: relative;
  opacity: 1;
}

.c-documents > .c-container {
  margin-bottom: 50px;
  width: 100%;
}

.c-documents .c-searchBox {
  width: auto;
}

.c-documents input {
  width: calc(100% - 60px);
  background: transparent;
}

@media (max-width: 670px) {
  .outerContainer {
    padding: 10px;
  }
}

@media (max-width: 768px) {
  .c-documents {
    padding-top: 15px;
  }
}

.c-selectedFilters {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 10px 0;
}

.c-tags .c-container {
  width: auto;
  margin: 5px;
  padding: 7px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.c-tags span {
  width: auto;
  min-width: 0;
  line-height: 30px;
  display: inline-block;
  padding-right: 10px;
}

.c-selectedFilters .icon {
  padding: 0 5px 0 0;
  position: relative;
  display: inline-block;
}



