.c-drawer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid;
  text-align: left;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 -2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
          box-shadow: 0 -2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
  -webkit-transition: -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  -o-transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  transition: box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1), -webkit-box-shadow 200ms cubic-bezier(0.4,0.0,0.2,1);
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.c-drawer.active {
  height: 101%;
}

.c-drawer .c-drawer--header {
  min-height: 30px;
  padding: 10px;
  cursor: pointer;
}

.c-drawer .c-drawer--text {
  display: inline-block;
  margin-left: 5px;
  line-height: 30px;
  margin-left: 35px;
  font-size: large;
  font-weight: lighter;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 70vw;
}

.c-hamburger {
  position: absolute;
  right: 20px;
  top: 10px;
}

.c-hamburger .line{
  width: 40px;
  height: 5px;
  background-color: #333333;
  display: block;
  margin: 4px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.c-hamburger:hover{
  cursor: pointer;
}

.c-hamburger.active .line:nth-child(2){
  opacity: 0;
}

.c-hamburger.active .line:nth-child(1){
  -webkit-transform: translateY(13px) rotate(45deg);
      -ms-transform: translateY(13px) rotate(45deg);
          transform: translateY(13px) rotate(45deg);
  margin: 5px auto;
}

.c-hamburger.active .line:nth-child(3){
  -webkit-transform: translateY(-13px) rotate(-45deg);
      -ms-transform: translateY(-13px) rotate(-45deg);
          transform: translateY(-13px) rotate(-45deg);
  margin: 10px auto;
}

.c-hamburger.active {
  right: 20px;
  top: 25px;
}

.c-drawer .c-drawer--title {
  text-align: center;
}

@media (max-width: 768px) {

  .c-drawer .c-drawer--text {
    font-size: medium;
  }

  .c-drawer--title {
    font-size: large;
    padding: 0px 40px;
  }

  .c-hamburger.active {
    top: 13px;
    right: 12px;
  }

  .c-hamburger {
    top: 12px;
    right: 16px;
  }

  .c-hamburger .line {
    width: 26px;
    height: 3px;
  }

  .c-hamburger.active .line {
    margin: 10px auto;
  }

}



